.recommendedVideos{
    flex: 0.8;
    background-color: #f9f9f9;
    padding: 40px 20px;
    padding-bottom: 0;
}
.recommendedVideos > h2 {
    margin-left: 5px;
    margin-bottom: 20px;
}

.recommendedVideos__Videos {
    display: flex;
    flex-wrap: wrap;
}
@media (max-width: 575.89px){
    .recommendedVideos{
        flex: 1;
    }
    .recommendedVideos__Videos{
        justify-content: center;
    }
}