.videoCard {
    width: 270px;
    margin-bottom: 20px;
}

.videoCard__thumbnail{
    height: 140px;
    width: 250px;
}

.videoCard__info{
    display: flex;
    margin-top: 10px;
    padding-right: 30px;
}

.videoCard__text{
    margin-left: 15px;
}

.videoCard__text > h4 {
    font-size: 14px;
    margin-bottom: 5px;
}

.videoCard__text > p {
    font-size: 14px;
    color: gray;
}

.videoCard__avatar{
    height: 30px !important;
    width: 30px !important;
}

@media (max-width: 575.89px){
    .videoCard{
        width: 90%;
        margin-bottom: 40px;
    }
    .videoCard__thumbnail{
        height: 70%;
        width: 98%;
        object-fit: fill;
    }
}